.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fondo {
  background-image: url("./images/fron2.jpg");
  overflow: hidden;
  background-position: 0px -130px;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  max-height: 100vh;
  min-height: 85vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.fondo h2 {
  display: block;
  color:#f7f6fc;
  margin-top: 25%;
  margin-bottom: -3%;
}

.fondo p{
  color: white;
  font-size: 50px;
  margin-bottom: 5%;
}
.fondo p a{
  transition: all 300ms;
}

.layout-2 {
  margin: 0 auto;
  display: flex;
  width: 100%;
  margin-left: 10%;

}

.ladoizqContacto img{
  width: 100%;
  margin-left: 0% !important;

}
.cardContacto .ladoizqContacto::after{
    background: none !important;
    border-radius: none !important;
    box-shadow: 3px -2px 13px 0px #525252 ;
    content: none !important;
    height: 100%;
    width: 100%;
  
}

.cuerpo-texto{
  padding-left: 15px;
}


.imgcontacto{
  flex-grow: 0;
}

.imgcontacto span{
  font-size: 42vh;
}

.text-contacto{
  flex-grow: 1;
}

.header {
  display: flex;
  height: 100px;
  justify-content: center;
  align-items: center;
  background-color: #3a3a3a;
  color: antiquewhite;
}

.header .logo img {
  display: block;

  height: 258px;
}

.header .logo {
  display: block;
  flex-grow: 1;
  margin-right: -20%;
}

.header nav {
  flex-grow: 5;
}

.header nav ul {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
}

.header li {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 16px;
  margin-left: 15px;
  margin-right: 15px;
}

.header li a {
  color: black;
  text-decoration: none;
  transition: all 300ms;
  color: whitesmoke;
}

.header a:hover {
  color: rgb(231 212 33);
}

.logo {
  color: white;
}

.footer{
  text-align: center;
  font-size: 14px;
  border-top: 3px solid black;
  background-color: #3a3a3a;
  height: 150px;
  line-height: 6px;
}
.footer h5{
  color: rgb(231 212 33);
  font-size: 20px;
  margin-bottom: -1%;
}
#menu-footer ul li {
  list-style: none;
}

.footer{
  display: flex;
  color: whitesmoke;
}

.footer #menu-footer{
  flex-grow: 1;
  margin-top: -4%;
}
.footer #redes{
  flex-grow: 1;
}
.footer #contacto{
  flex-grow: 1;
}

#menu-footer img{
  display: block;

  margin-left: 15%;
  height: 258px;
  margin-top: 0%;
}

#contacto p a{
  text-decoration: none;
  color: white;
  transition: all 300ms;
}

#contacto p a:hover{
  color: rgb(231 212 33);
}

.footer h2{
  display: block;
  color: rgb(231 212 33);
}

.icons-redes a{
  font-size: 50px;
  color:white;
}
.description{
  width: 88%;
  margin: 0 auto;
}
.description p{
  font-size: 25px;
}
#titulo-letra{
  text-transform: uppercase;
  margin-top: 5%;
  font-weight: bold;
  color: rgb(86, 86, 126);
}

#titulo-letra h2{
  font-weight: bold;
  color: rgb(24, 24, 131);
}

.skewed {
  position: absolute;
  top: 137%;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 26%;
  overflow: hidden;
  background: #f4f1ebdb;
  z-index: -1;
  transform: skewY(6deg);
  transform-origin: top right;
  max-height: 40%;
  line-height: 10%;
}

