  border-radius: 5%;
}

.project img{
  width: 100%;
  transition: all 300ms;
}
.project img:hover{
    color: #37bcf9;
    transform: scale(1.2, 1.2);
}


.image-wrap img{
  border-radius: 5%;
 height: 200px;
 width: 200px;
}

.project-info{
  padding: 10px;
}

.project-info a{
