  color: black;
  transition: all 300ms;
}

.project-info a:hover{
  color: rgb(194, 180, 49);
}

.project-info p{
  color: grey;
  font-size: 16px;
}

.container-contact{
  display: flex;
  flex-direction: column;
  margin-top: 5%;
}

.log-contact{
  font-size: 52px;
  line-height: 96px;
}

.text-contacts{
  position: absolute;
  margin-top: 4vh;
  margin-left: 1vh;
  text-decoration: none;
  color: #282c34;
}

.text-contacts:hover{
  color: rgb(231 212 33);
}

.map{
  margin-left: 50vh;
  margin-top: 15vh;
  width: 100vh;
  height: 100px;
}

.card-caja{
  display: block;
  border: 2px solid rgba(10, 10, 10, 0.486);
  width: 60%;
