/*Estilos cards eventos*/

.card h2{
  font-size: 2rem;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: .5rem;
  margin-top: 0;
}
.bold-eventos-card{
  font-weight: bold;
  font-size: 20px;
}
.card{
  border-radius: 20px;
  box-shadow: 4px 4px 4px 3px #c9c2c2;
  display: flex;
  align-items: center;
  overflow: hidden;
  max-width: 1000px;
  height: 100%;
  margin: 5% 16% 5% 16%;


}
.card .ladoIzq{
  color: white;
  padding: 48px;
  position: relative;
  width: 65%;
}

.ladoIzq img{
  max-height: 398px;
  border-radius: 5%;
  margin-left: 15%;
}
.card .ladoIzq::after{
  background: #000000d1;
  border-radius: 24px;
  box-shadow: 3px -2px 13px 0px #525252 ;
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  transform: skew(160deg,0);
  transform-origin: top left;
  width: 100%;
  z-index: -1;
}
.card .ladoDer{
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-right: 12px;
  width: 35%;
}
.card .cuerpo{
  line-height: 150%;
  text-align: center;
}
.card .centrahrz{
  text-align: center;
}

.btn {
  text-decoration: none;
  color: white;
  background-color: #b82e1f;
  border-radius: 5px;
  display: inline-block;
  margin-top: 18px;
  padding: 14px;
  transition: all 300ms;
}

.btn:hover{
  transform: scale(1.2, 1.2);
}



